// src/middleware.ts
import { NextRequest, NextResponse } from "next/server";
import { adminAuthMiddleware } from "./middlewares/adminAuth";  // Import admin middleware
import { logMessage } from "./utils/commonUtils";

export function middleware(req: NextRequest) {
    const res = NextResponse.next();
    // Apply CORS headers globally
    res.headers.set("Access-Control-Allow-Origin", "*");  // Change '*' to your frontend URL in production
    // res.headers.set("Access-Control-Allow-Methods", "GET, POST, PUT, DELETE, OPTIONS");
    res.headers.set("Access-Control-Allow-Methods", "*");
    res.headers.set("Access-Control-Allow-Headers", "Content-Type, Authorization");

    console.log(`req.method: ${req.method}`);
    console.log(`req.url: ${req.url}`);

    // Handle preflight requests
    if (req.method === "OPTIONS") {
        return new Response(null, { status: 200, headers: res.headers });
    }

    // Apply adminAuthMiddleware to /api/admin/list route
    const adminProtectedRoutes = ["/api/admin/list", "/api/admin/auth/verify"];
    const dropshipperProtectedRoutes = ["/api/dropshipper/list", "/api/dropshipper/auth/verify"];
    const supplierProtectedRoutes = ["/api/supplier/list", "/api/supplier/auth/verify", "/api/supplier/profile/:path"];
    const restProtectedRoutes = ["/api/category", "/api/category/:path*", "/api/brand", "/api/brand/:path*", "/api/warehouse", "/api/warehouse/:path*", "/api/location/country", "/api/location/country/:path*", "/api/location/state", "/api/location/state/:path*", "/api/location/city", "/api/location/city/:path*", "/api/product", "/api/product/:path*", "/api/supplier", "/api/supplier/:path*", "/api/dropshipper", "/api/dropshipper/:path*"];

    if (adminProtectedRoutes.some(route => req.url.includes(route))) {
        logMessage(`debug`, `URL under AdminProtectedRoutes`);
        const applicableRoles = ["admin", "admin_staff"];
        const adminRole = "admin";
        return adminAuthMiddleware(req, adminRole, applicableRoles);
    }

    if (dropshipperProtectedRoutes.some(route => req.url.includes(route))) {
        logMessage(`debug`, `URL under dropshipperProtectedRoutes`);
        const applicableRoles = ["dropshipper", "dropshipper_staff"];
        const adminRole = "dropshipper";
        return adminAuthMiddleware(req, adminRole, applicableRoles);
    }

    if (supplierProtectedRoutes.some(route => req.url.includes(route))) {
        logMessage(`debug`, `URL under supplierProtectedRoutes`);
        const applicableRoles = ["supplier", "supplier_staff"];
        const adminRole = "supplier";
        return adminAuthMiddleware(req, adminRole, applicableRoles);
    }

    if (restProtectedRoutes.some(route => req.url.includes(route))) {
        logMessage(`debug`, `URL under restProtectedRoutes`);
        const applicableRoles = ["admin", "admin_staff", "dropshipper", "dropshipper_staff", "supplier", "supplier_staff"];
        const adminRole = "admin";
        return adminAuthMiddleware(req, adminRole, applicableRoles);
    }

    return req;  // Continue processing for other routes
}

// Define the matcher for specific routes
export const config = {
    matcher: ["/api/admin/list", "/api/admin/auth/verify", "/api/dropshipper/list", "/api/dropshipper/auth/verify", "/api/supplier/list", "/api/supplier/auth/verify", "/api/supplier/profile/:path", "/api/category", "/api/category/:path*", "/api/brand", "/api/brand/:path*", "/api/warehouse", "/api/warehouse/:path*", "/api/location/country", "/api/location/country/:path*", "/api/location/state", "/api/location/state/:path*", "/api/location/city", "/api/location/city/:path*", "/api/product", "/api/product/:path*", "/api/supplier", "/api/supplier/:path*", "/api/dropshipper", "/api/dropshipper/:path*"],
};
